(function() {
    'use strict';

    angular
        .module('donna')
        .controller('MatchController', MatchController);

    /** @ngInject */
    function MatchController($state, Match, SweetAlert) {
        var vm = this;
        var stateMethod = $state.current.method;
        vm.readyToUpload = true;

        // List
        if (stateMethod == 'list') {
            vm.matches = [];

            Match.getMatches().success(function (data) {
                vm.matches = data;
            }).error(function (err) {
                SweetAlert.swal(err.error, err.summary, 'error');
            });
        }

        // Update
        if (stateMethod == 'update') {
            Match.getOne($state.params.id).success(function (data) {
                data.date = moment(data.date).utc().format("YYYY-MM-DD HH:mm");
                vm.match = data;
            }).error(function (err) {
                SweetAlert.swal(err.error, err.summary, 'error');
            });
        }

        // Create
        if (stateMethod == 'create') {
            vm.match = {};
        }

        // Single
        if (stateMethod == 'single') {
            Match.getOne($state.params.id).success(function (data) {
                vm.match = data;
                vm.messages = data.messages;
                vm.msg = {};

            }).error(function (err) {
                SweetAlert.swal(err.error, err.summary, 'error');
            });
        }


        vm.newMsg = function () {
            if (vm.msg.length) {
                vm.readyToUpload = false;
                Match.newMessage($state.params.id, vm.msg).success(function (match) {
                    vm.messages = match.messages;
                    vm.readyToUpload = true;
                    vm.msg = '';

                    SweetAlert.swal({
                        title: 'Success',
                        text: 'Message successfully sent',
                        timer: 1000,
                        showConfirmButton: false,
                        type: 'success'
                    });

                }).error(function (err) {
                    vm.readyToUpload = true;
                    SweetAlert.swal(err.error, err.summary, 'error');
                });
            }
        };

        vm.save = function () {
            vm.readyToUpload = false;
            vm.match.date = moment(vm.match.date).utc();
            var action = (stateMethod == 'update') ? Match.update($state.params.id, vm.match) : Match.create(vm.match);

            action.success(function (news) {
                vm.readyToUpload = true;
                SweetAlert.swal({
                    title: 'Success',
                    text: 'Data successfully saved',
                    timer: 1000,
                    showConfirmButton: false,
                    type: 'success'
                });

                $state.go('user.matches');

            }).error(function (err) {
                console.log(err);
                vm.readyToUpload = true;
                SweetAlert.swal(err.error, err.summary, 'error');
            });
        };

        vm.destroy = function (id) {
            Match.destroy(id).success(function () {
                vm.matches = _.reject(vm.matches, function (match) {
                    return id == match.id;
                });

                SweetAlert.swal({
                    title: 'Success',
                    text: 'Data successfully deleted',
                    timer: 1000,
                    showConfirmButton: false,
                    type: 'success'
                });
            }).error(function (err) {
                SweetAlert.swal(err.error, err.summary, 'error');
            });
        };

        vm.destroyMsg = function (id) {
            Match.destroyMsg(id).success(function () {
                vm.messages = _.reject(vm.messages, function (msg) {
                    return id == msg.id;
                });

                SweetAlert.swal({
                    title: 'Success',
                    text: 'Data successfully deleted',
                    timer: 1000,
                    showConfirmButton: false,
                    type: 'success'
                });
            }).error(function (err) {
                SweetAlert.swal(err.error, err.summary, 'error');
            });
        };
    }

})();

(function () {
    'use strict';

    angular
        .module('donna')
        .factory('LocalService', LocalService)
        .factory('Auth', Auth)
        .factory('AuthInterceptor', AuthInterceptor);

    /** @ngInject */
    function LocalService() {
        return {
            get: function (key) {
                return localStorage.getItem(key);
            },
            set: function (key, val) {
                return localStorage.setItem(key, val);
            },
            unset: function (key) {
                return localStorage.removeItem(key);
            }
        }
    }

    function Auth($http, LocalService, AccessLevels, API, $window) {
        return {
            authorize: function (access) {
                if (access === AccessLevels.user) {
                    return this.isAuthenticated();
                } else {
                    return true;
                }
            },
            isAuthenticated: function () {
                return LocalService.get('auth_token');
            },
            login: function (user) {
                var login = $http.post(API.URL + '/api/auth/login', user);
                login.success(function (result) {
                    console.log(result);
                    LocalService.set('auth_token', angular.toJson(result));
                });
                return login;
            },
            logout: function () {
                // The backend doesn't care about logouts, delete the token and you're good to go.
                LocalService.unset('auth_token');
                $window.location.reload();
            }
        }
    }

    function AuthInterceptor($q, $injector) {
        var LocalService = $injector.get('LocalService');

        return {
            request: function (config) {
                var token;
                if (LocalService.get('auth_token')) {
                    token = angular.fromJson(LocalService.get('auth_token')).token;
                }
                if (token) {
                    config.headers.Authorization = 'Bearer ' + token;
                }
                return config;
            },
            responseError: function (response) {
                if (response.status === 401 || response.status === 403) {
                    LocalService.unset('auth_token');
                    $injector.get('$state').go('anon.login');
                }
                return $q.reject(response);
            }
        }
    }

})();

